<template>
  <div class="app-wrap">
    <app-header :transDataSet="transDate"></app-header>
    <div class="app-context">
      <div class="about">
        <h1 @click="checkRatingPop()"> </h1>
      </div>
      <div class="about">
        <h1 @click="checkPop()"> </h1>
      </div>
    </div>
    <app-footer :transOrder="iconOrderValue"></app-footer>
    <add-popup v-if="checkPopup" :popupSet="popupSet" />
  </div>
</template>

<script>
import AddPopup from "@/components/AddPopup";
import AppFooter from "@/components/AppFooter";
import AppHeader from "@/components/AppHeader";
import { mapMutations, mapState } from "vuex";
export default {
  name: "",
  data() {
    return {
      transDate: {
        headerName: "",
        prevUrl: "/",
        leftButton: "menu",
        rightButton: "alarm"
      },
      popupSet: {
        confirmBtnText: "",
        nextLink: "/"
      },
      iconOrderValue: {
        iconOrder: 1
      }
    };
  },
  created() {},
  methods: {
    checkRatingPop() {
      this.popupSet.count = "rating";
      this.SET_POPUP(true);
      this.popupSet.title = "  ?";
      this.popupSet.content =
        "&quot;&quot;        .";
    },
    checkPop() {
      const transPopItem = [];
      const item1 = {
        title: "[]",
        content:
          "     , ,   ."
      };
      const item2 = {
        title: "[]",
        content:
          "        48   .     7   ."
      };
      transPopItem.push(item1);
      transPopItem.push(item2);
      this.popupSet.count = "inImgPop";
      this.popupSet.inPopItem = transPopItem;
      this.SET_POPUP(true);
      this.popupSet.modalImg = "burnPop.svg";
      this.popupSet.title = undefined;
      this.popupSet.content = undefined;
    },
    ...mapMutations("basic", ["SET_POPUP"])
  },
  computed: {
    ...mapState("basic", ["checkPopup"])
  },
  components: {
    AppHeader,
    AddPopup,
    AppFooter
  }
};
</script>
<style lang="scss" scoped></style>
